.footer {
  color: $greydark;
  position: relative;

  &::after,
  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: $grey;
    opacity: 0.2;
    bottom: 60px;

    @media (max-width: $tablet-width) {
      bottom: 50px;
    }
  }

  &::before {
    content: none;

    @media (max-width: $desktop-width) {
      content: "";
      top: 100px;
    }

    @media (max-width: $tablet-width) {
      top: 200px;

      box-shadow: 0 300px 0 0 $grey;
    }
  }
}

.footer__top {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 20px;
  padding-top: 60px;
  margin-bottom: 50px;

  @media (max-width: $desktop-width) {
    grid-template-columns: repeat(2, 1fr);
    padding-top: 20px;
  }
  @media (max-width: $tablet-width) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.footer__business-card {
  @media (max-width: $desktop-width) {
    grid-column: 1/3;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    margin-bottom: 50px;
  }

  @media (max-width: $tablet-width) {
    grid-column: 1/2;
    flex-direction: column;
  }
}

.footer__logo {
  @media (max-width: $tablet-width) {
    align-self: center;
    margin-bottom: 80px;
  }
}

.footer__navigation {
  position: relative;
}

.footer__adress {
  justify-self: end;
  position: relative;

  @media (max-width: $tablet-width) {
    justify-self: start;
    width: 100%;
  }
}

.footer__button {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
    border: none;
    width: 30px;
    height: 30px;
    position: absolute;
    background-color: transparent;
    cursor: pointer;
    top: 0;
    right: 0;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: $grey;
      opacity: 0.5;
      width: 16px;
      height: 1px;
      top: 10px;
      right: 6px;
    }

    &::after {
      transform: rotate(90deg);
    }
  }
}

.footer__bottom {
  padding: 20px 0;
}
