.modal-form {
  background-color: #00B2FF;
  width: 50%;
  padding: 33px 39px;
  color: #ffffff;
  margin: 0 auto;
  position: fixed;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  display: none; }
  @media (max-width: 1023px) {
    .modal-form {
      display: none; } }

.modal-form__title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 28px;
  line-height: 39px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 20px; }

.modal-form__text {
  margin: 0;
  width: 380px;
  margin-bottom: 20px; }

.modal-form__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 20px; }

.modal-form__item input,
.modal-form__item textarea {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  padding-left: 16px;
  color: #ffffff; }
  .modal-form__item input::placeholder,
  .modal-form__item textarea::placeholder {
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
    opacity: 1;
    padding-bottom: 16px;
    font-family: "PTMono", "Arial", sans-serif; }

.modal-form__item input {
  height: 50px; }

.modal-form__item textarea {
  min-height: 104px;
  padding-top: 16px; }

.modal-form__button {
  width: 100%; }

.modal-form__checkbox {
  position: relative;
  font-size: 14px;
  line-height: 16px;
  padding-left: 20px;
  display: block;
  opacity: 0.8;
  cursor: pointer; }
  .modal-form__checkbox:hover, .modal-form__checkbox:focus {
    opacity: 0.5; }

.modal-form__check {
  position: absolute;
  width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
  left: 0px; }

.modal-form__check svg {
  stroke: #ffffff;
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%);
  stroke: transparent; }

.modal-form__checkbox input {
  display: none; }

.modal-form__checkbox input:checked + .modal-form__check svg {
  stroke: #ffffff; }

.modal__button {
  display: block;
  border: none;
  width: 30px;
  height: 30px;
  position: absolute;
  background-color: transparent;
  cursor: pointer;
  top: 20px;
  right: 20px; }
  .modal__button::before, .modal__button::after {
    content: "";
    position: absolute;
    background-color: #ffffff;
    width: 16px;
    height: 1px;
    top: 10px;
    right: 6px; }
  .modal__button::after {
    transform: rotate(45deg); }
  .modal__button::before {
    transform: rotate(-45deg); }
